import React from 'react'
import { useState, useEffect } from 'react'
import { allCells } from '../../datacells/datacell'
import Accordion from './accordion'
import cl from './HomePage.module.css'

import { smthAbouteBlood,about–°ellFormation,aboutPlateles,aboutLeuco,aboutErythro,} from './../../datacells/textSection';
import HomePageText from '../HomePageText'
import SearchTwoToneIcon from '@mui/icons-material/SearchTwoTone'
import RemoveButton from './RemoveButton'


const [width, height,align,fullScreen] = [16,32,1,100]

const allCellsNames = allCells.map(cells => cells.name)
const randomAllCellsNamesIndex = Math.floor(Math.random() * allCellsNames.length) // –ø–æ–ª—É—á–∞–µ—Ç —Å–ª—É—á–∞–π–Ω–æ–µ –∏–Ω–¥–µ–∫—Å –º–∞—Å—Å–∏–≤–∞ —Å –Ω–∞–∑–≤–∞–Ω–∏—è–º–∏ –∫–ª–µ—Ç–æ–∫


export default function HomePage () {

    const [cellsName, setCellsName] = useState([])
    const [inputValue, setInputValue] = useState(`${allCellsNames[randomAllCellsNamesIndex].toLowerCase()}`)//–∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä—É–µ—Ç –Ω–∞—á–∞–ª—å–Ω–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ –∏–Ω–ø—É—Ç–∞ —Å–ª—É—á–∞–π–Ω–æ–π –∫–ª–µ—Ç–∫–æ–π 

    
    useEffect(() => {
        inputValue.length 
        ? setCellsName(allCells) 
        : setCellsName([])
    }, [inputValue]);

    const filtredCells = cellsName.filter(item => { 
         return item.name.toLowerCase().includes(inputValue.toLowerCase())  
    })

    // const removeButton = () => {
    //     cellsName 
    //         ? setCellsName([]) || setInputValue('') //–æ–±–Ω—É–ª—è–µ—Ç —Å–æ—Å—Ç–æ—è–Ω–∏—è,–≤–æ –∏–∑–±–µ–∂–∞–Ω–∏–µ –º–µ–ª—å–∫–∞–Ω–∏—è –º–∞—Å—Å–∏–≤–∞ —Å –∫–ª–µ—Ç–∫–∞–º–∏ –ø—Ä–∏ –Ω–∞–∂–∞—Ç–∏–∏ –Ω–∞ –∫–Ω–æ–ø–∫—É "–•"
    //         : <p className={cl.notFound}>–ü—Ä–æ–∏–∑–æ—à–ª–∞ –æ—à–∏–±–∫–∞, –ø–µ—Ä–µ–∑–∞–≥—Ä—É–∑–∏—Ç–µ —Å—Ç—Ä–∞–Ω–∏—Ü—É</p>
    //     }
     
    return (

    <div className={cl.container}>
        <div className={cl.containerHomePageText}>
            <HomePageText 
                h1={smthAbouteBlood.h1}
                p={smthAbouteBlood.p} 
                img={smthAbouteBlood.img}
                style={{marginRight: align + `em`, width: height +'em',height: width + 'em'}} //–≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω–æ–µ —Ä–∞—Å–ø–æ–ª–æ–∂–µ–Ω–∏–µ –∏–ª–∏ –≤–µ—Ä—Ç–∏–∫–∞–ª—å–Ω–æ–µ
                align='left' // —Ñ–æ—Ç–æ —Å–ª–µ–≤–∞ –∏–ª–∏ —Å–ø—Ä–∞–≤–∞
                />
            <HomePageText 
                h1={about–°ellFormation.h1} 
                p={about–°ellFormation.p} 
                img={about–°ellFormation.img} 
                style={{marginLeft: align + `em`, width: height +'em',height: width + 'em'}}
                align='right'
            />
            <HomePageText 
                h1={aboutErythro.h1} 
                p={aboutErythro.p} 
                img={aboutErythro.img} 
                style={{marginRight: align + `em`, width: width +'em',height: height + 'em'}} 
                align='left'
            />
            <HomePageText 
                h1={aboutLeuco.h1} 
                p={aboutLeuco.p} 
                img={aboutLeuco.img} 
                style={{marginLeft: align + `em`, width: width +'em',height: height + 'em', }} 
                align='right'
            />
            <HomePageText 
                h1={aboutPlateles.h1} 
                p={aboutPlateles.p} 
                img={aboutPlateles.img} 
                style={{marginRight: align + `em`, width: fullScreen +'%',height: width + 'em'}}
                align='left'
            />
        </div>
       <section className={cl.countainer_Search}>
            <div className={cl.container_input}>
                   
                    <input
                    required
                    id='input'
                    className={cl.input}
                    type="text"
                    list='auto_complete'
                    value={inputValue}
                    onChange={e => setInputValue(e.target.value)}
                    spellCheck={false}
                    />  
                    <label for='input'>–ø–æ–∏—Å–∫ –∫–ª–µ—Ç–æ–∫...</label>
                <SearchTwoToneIcon className={cl.iconSearch}/>
                <RemoveButton className={inputValue ? cl.removeBtnActive : cl.removeBtn } resetCellsName={setCellsName} removeInputValue={setInputValue} />   


                <datalist id='auto_complete' >
                    {
                    allCells.map((item) =>
                        <option key={item.id} value={item.name.toLowerCase()}></option>
                    )}
                </datalist>
            </div>
            

            <div className={cl.container_accordion}>
                {
                filtredCells.length || !inputValue 
               ? (filtredCells.map ((item)=>{ 
                return (
                    <Accordion 
                    key = {item.id}
                    name =  {item.name.toLowerCase()}
                    size={item.size} 
                    cytoplasm={item.cytoplasm}
                    localization={item.localization}
                    function={item.function}
                    images= {item.image}
                    avatar = {item.avatar}
                    />
                )
                }))
               : <p className={cl.notFound}>–ù–ò–ß–ï–ì–û –ù–ï –ù–ê–ô–î–ï–ù–û</p>
                }
            </div>
        </section>
    </div>
   
        )
        
    
}
